import { Circle, Tag, Animation } from 'konva';

class Button extends Circle {
	constructor(x, y, color, radius, ctx) {
		super({
			x: x,
			y: y,
			radius: radius,
			fill: color,
			stroke: 'black',
			strokeWidth: 4, 
			draggable: false,
	        shadowColor: 'black',
	        shadowBlur: 10,
	        shadowOffset: 10,
	        shadowOpacity: 0.5
		});
		console.log(`Creating ${this.className}`);
		this.type = 'Button';
		this.id = this.type + ' #' + Math.floor(Math.random() * 1000);
		this.on('mouseover', function() {
			document.body.style.cursor = 'pointer';
		});
		this.on('mouseout', function() {
			document.body.style.cursor = 'default';
		});
		this.on('mousedown', function(e) {
			(new Animation((frame) => {
				this.scale({x: 0.9, y: 0.9});
			}, ctx)).start();
		});
		this.on('mouseup', function(e) {
			(new Animation((frame) => {
				this.scale({x: 1, y: 1});
			}, ctx)).start();
		});
	}
}

export default Button;


