import 'babel-polyfill';
import '../styles/index.scss';
import Stand from './StandComponents/Stand.js';
import Button from './StandComponents/Button.js';
import Screen from './StandComponents/Screen.js';
import Wire from './StandComponents/Wire.js';
import Input from './StandComponents/Input.js';

const stand = new Stand();
let wiringState = false, wiringInput1, wiringInput2;

let btn_1 = new Button(
	stand.stage.getWidth() / 2,
	stand.stage.getHeight() / 2,
	'red',
	25,
	stand
);
let btn_2 = new Button(
	stand.stage.getWidth() / 2 + 100,
	stand.stage.getHeight() / 2,
	'green',
	25,
	stand
);
let screen_1 = new Screen(
	stand.stage.getWidth() / 2,
	stand.stage.getHeight() / 2 + 100,
	'black',
	'#f2f2f2',
	'1200'
);
let i1 = new Input(
	100,
	100,
	stand
);
let i2 = new Input(
	300,
	100,
	stand
);
let i3 = new Input(
	300,
	600,
	stand
);
let inputs = [i1, i2, i3];

/*inputs.on('click', function(e) {
	if (wiringState === false) {
		wiringState = true;
		wiringInput1 = e.target;
	}
})**/
//let wire_1 = new Wire(50,50,stand);
stand.addElements([i1, i2, i3, btn_1, btn_2, screen_1]);
console.log(`Elements .. ${stand.getElements()}`);



