import { Label, Text, Tag } from 'konva';

class Screen extends Label {
	constructor(x, y, textColor, backgroundColor, text) {
		super({
			x: x,
			y: y,
			opacity: 1
		});
		console.log(`Creating ${this.className}`);
		this.type = 'Screen';
		this.id = this.type + ' #' + Math.floor(Math.random() * 1000);
		this.add(new Tag({
			fill: backgroundColor,
			stroke: '#333',
			strokeWidth: 2,
			pointerDirection: 'down',
	        pointerWidth: 10,
	        pointerHeight: 10,
	        lineJoin: 'round',
	        shadowColor: 'black',
	        shadowBlur: 10,
	        shadowOffset: 10,
	        shadowOpacity: 0.5
		}));
		this.add(new Text({
			text: text,
			fontFamily: 'Calibri',
			fontSize: 18,
			padding: 5,
			fill: textColor
		}));
	}
}

export default Screen;


